import React from "react";
import styled from "styled-components";

const Footer = () => {
	const Powerby = styled.div`
		display: flex;
		padding: 1rem;
		width: 1024px;
		margin: 0 auto;
		justify-content: center;
	`;

	return (
		<div>
			<Powerby>
				<a href="http://developers.neople.co.kr">Neople 오픈 API</a>
			</Powerby>
		</div>
	);
};

export default Footer;
